import { createRouter, createWebHistory } from 'vue-router'
import Index from '@/views/Index.vue'
import Earphone from '@/views/Earphone.vue'
import EarphoneItem from '@/views/EarphoneItem.vue'

const routes = [
  {
    path: '/',
    name: 'index',
    component: Index
  },
  {
    path: '/earphone',
    name: 'earphone',
    component: Earphone
  },
  {
    path: '/item0001',
    name: 'earphoneItem',
    component: EarphoneItem
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior (to, from, savedPosition) {
    return {
      el: '#app',
      top: 0
    }
  }
})

export default router
