      <article class="list">
        <div class="item">
          <div class="head">
            <p class="title">애플 유저라면 고민없이 구입하자</p>
            <div class="rate">
              <svg height="512" viewBox="0 0 48 48" width="512" xmlns="http://www.w3.org/2000/svg"><g id="_x35_-Star"><path d="m42.9726563 19.6572266c-.1308594-.4047852-.5048828-.6826172-.9306641-.6914063l-12.8710938-.2617188-4.2265625-12.1230468c-.1396484-.4018555-.5185546-.6708984-.9443359-.6708984s-.8046875.269043-.9443359.6708984l-4.2265625 12.1230469-12.8710938.2617187c-.4257813.0087891-.7998047.2866211-.9306641.6914063-.1318359.4052734.0087891.8496093.3486329 1.1064453l10.2568359 7.7529297-3.7275391 12.2827148c-.1230469.4072266.0244141.8481445.3681641 1.0991211.3457031.2504883.8095703.2558594 1.1591797.0131836l10.5673828-7.3325195 10.5673828 7.3325195c.171875.1186523.3710938.1782227.5703125.1782227.2070313 0 .4130859-.0639648.5888672-.1914063.34375-.2509766.4912109-.6918945.3681641-1.0991211l-3.7275391-12.2827148 10.2568359-7.7529297c.3398438-.256836.4804688-.7011719.3486329-1.1064453z"/></g></svg>
              <p>5</p>
            </div>
          </div>
          <textarea readonly>
            1. 가격
            솔직히 비쌉니다.

            2. 성능
            가격은 비싸지만 지금까지 써본 것 중에서 노이즈캔슬링과 통화품질은 에어팟이 최고인 것 같습니다. 지금까지 브리츠 등의 중저가를 쓰다가 이번에 구매한건데 왜 아직 이걸 안 사고 있었나라는 후회가 됐습니다.
            아이폰을 쓰시는 분들이라면 강추드립니다. 각종 음악듣기, 블루투스 간 상호전환 등이 직관적이고 편합니다. 또한, 충전중 표시를 아이폰 아이콘으로 보여주고, 마지막 접속 위치찾기가 가능합니다.
            배터리도 상당히 길어서 서울, 부산 기차타고 갈 때까지 계속 들을 수 있습니다.

            3. 음질
            약간 청량감이 빠진 느낌입니다. 그래도 조용한 곳에 있으면 블루투스 이어폰인지 모를 정도로 좋습니다.
            또한, 밖에 있을 때 노이즈캔슬링 기능을 켜고 통화를 하면 통화내용에 온전히 집중할 수 있어서 좋습니다.
          </textarea>
          <div class="bottom">
            <p class="writer">프리덤맘</p>
            <p class="date">2023.06.10</p>
          </div>
        </div>
