<script>
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Thumbs, EffectFade } from 'swiper'
import 'swiper/css'
import { onMounted } from 'vue'

export default {
  name: 'EarphoneItem',
  components: {
    Swiper,
    SwiperSlide
  },
  data () {
    return {
      thumbsSwiper: null,
      modules: [Thumbs, EffectFade]
    }
  },
  methods: {
    setThumbsSwiper (swiper) {
      this.thumbsSwiper = swiper
    }
  },
  setup () {
    onMounted(() => {
      const state = document.querySelector('.count')
      const price = document.querySelector('.sellPrice')
      const amount = document.querySelector('.amountInput')
      const add = document.querySelector('.amountAdd')
      const remove = document.querySelector('.amountRemove')
      const sum = document.querySelector('.sumCost > .number')

      if (state) {
        sum.value = price.value

        let amountval = amount.value
        let sumval = sum.value
        let priceval = price.value

        if (add) {
          add.addEventListener('click', () => {
            amountval++
            sumval = amountval * priceval
            amount.value = amountval
            sum.value = sumval
            console.log(amountval, sumval, priceval)
          })
        }

        if (remove) {
          remove.addEventListener('click', () => {
            if (amountval > 1) {
              amountval--
              sumval = amountval * priceval
              amount.value = amountval
              sum.value = sumval
              console.log(amountval, sumval, priceval)
            } else {
              amountval = 1
            }
          })
        }
      }

      const detailImage = document.querySelector('.detail > .wrapImage')
      const detailToggle = document.querySelector('.wrapToggle > .toggle')

      detailToggle.addEventListener('click', () => {
        if (detailImage.classList.contains('on')) {
          detailImage.classList.remove('on')
          detailToggle.classList.remove('on')
        } else {
          detailImage.classList.add('on')
          detailToggle.classList.add('on')
        }
      })
    })
  }
}
</script>

<style scoped>
@import "@/styles/EarphoneItem.css";
</style>
